/* from policies start  */
.manage_slider_type_options {
    display: flex;
    justify-content: space-around;
    column-gap: 5px;
    flex-wrap: wrap;    
}
.manage_slide_selected_content {
    width: 250px;
    text-align: center;
    padding: 5px 20px;
    background-color: black;
    color: white;
    border-radius: 18px;
    border: 2px solid;
    margin-top: 5px;
    
}
.manage_slide_unselected_content {
    width: 250px;
    text-align: center;
    padding: 5px 20px;
    background-color: white;
    color: black;
    border: 2px solid black;
    border-radius: 18px;
    margin-top: 5px;
}

/* from policies end */


.mySwiper {
    width: 100%;
    padding-top: 50px;
    padding-bottom: 50px;
  }
  
  .swiper-slide {
    background-position: center;
    background-size: cover;
    width: 300px;
    height: 300px;
  }
  .swiper-slide.swiper-slide-visible.swiper-slide-active {
    padding: 10px;
    border: 6px solid black;
    border-radius: 15px;
    /* background-image: url("./../assets/about/Bounding\ box.png"); */
    background-color: white;
    position: relative;
  }
  .info_border{
    border: 3px solid black;
    font-size: 18px;
    font-weight: 700;
    white-space: nowrap;
  }
  .information{
    background-color: white;
    position: absolute;
    left: 0;
    right: 0;
    margin: 0 auto;
    width: 400px;
    /* margin-top: -10px; */
    z-index: 9; 
  }
  
  .swiper-slide img {
    display: block; 
    width: 100%;
    height: 100%;
    /* object-fit: cover; */
  }
  @media only screen and (max-width:600px){
    .swiper-slide.swiper-slide-visible.swiper-slide-active {
      width: 360px;
      aspect-ratio: 1.3;
  }
    .information{
      left: 0;
      right: 0;
      width: 330px;
      font-size: 15px;
    }
    .info_border{
      white-space: normal;
    }

  }
  @media only screen and (max-width:834px) and (min-width:600px){
    .swiper-slide.swiper-slide-visible.swiper-slide-active {
      width: 400px;
      /* height: 360px; */
      aspect-ratio: 1.3;
  }
    .information{
      left: 20%;
      right: 20%;
    }
  }
  @media only screen and (max-width:1024px) and (min-width:834px){
    .swiper-slide.swiper-slide-visible.swiper-slide-active {
      /* width: 360px; */
      /* height: 360px; */
      aspect-ratio: 1.3;
  }
  .swiper-slide{
    aspect-ratio: 1.3;
    /* height: 360px; */
  }
    .information{
      left: 20%;
      right: 20%;
    }
  }
  @media only screen and  (min-width:1025px){
    .swiper-slide.swiper-slide-visible.swiper-slide-active {
      /* width: 400px; */
      /* height: 360px; */
      aspect-ratio: 1.3;
  }
  .swiper-slide{
    /* height: 360px; */
    aspect-ratio: 1.3;
  }
    .information{
      left: 35%;
      right: 35%;
    }
  }