.whyus_card_container{
    display: flex;
    justify-content: space-around;
    margin-top: 40px;
}
.why_us{
    box-shadow: 2px 2px 5px black;
    width: 200px;
    border: 1px solid black;
    padding: 20px 0;
}
.manage_img_content_container{
    display: flex;
    justify-content: space-around;
}
.manage_img{
    width: 100%;
    height: 200px;
    margin: 0 auto;
}
.manage_content{
    width: 800px;
}

.manage_btn_div {
    display: flex;
    justify-content: center;
    align-items: center;
}
.manage_card_btn {
    border: 2px solid black;
    background-color: white;
    padding: 3px 15px;
    margin-top: 5px;
    border-radius: 20px;
}

.manage_card_btn:hover {
    background-color: black;
    border: 2px solid black;
    color: white;
}

@media only screen and (max-width:600px){
    .whyus_card_container{
        flex-wrap: wrap
    }
    .why_us{
        width: 300px;
        margin-top: 40px;
    }
    .manage_img_content_container{
        display: flex;
        flex-wrap: wrap;
    }
    .manage_content{
        width: 580px;
        order: 2;
        margin-top: 20px;
    }
    .manage_img{
        height: 300px;
    }
    .manage_img_div{
        order: 1;
        width: 300px;
    }
}

@media only screen and (max-width:834px) and (min-width:601px){
    .whyus_card_container{
        flex-wrap: wrap
    }
    .why_us{
        margin-top: 40px;
    }
    .manage_img_content_container{
        display: flex;
        flex-wrap: wrap;
    }
    .manage_content{
        width: 580px;
        order: 2;
        margin-top: 20px;
    }
    .manage_img_div{
        order: 1;
    }
    
    /* .trading_img{
        width: 150px;
        height: 150px;
    } */
}
@media only screen and (max-width:1024px) and (min-width:835px){
  
    .manage_content{
        width: 580px;
    }
   
}
@media only screen and (max-width:1280px) and (min-width:1025px){
    .manage_content{
        width: 700px;
    }
}